import {uploadFileToOss,updateCustInfo} from '@/common/api.js'

export default {
	data() {
		return {
			custInfo:{},
			changedImage:false
		};
	},
	onLoad() {
		this.COMMONFUNCTION.getStorageByKey('selectCustInfo').then(res=>{
			this.custInfo=res;
			this.COMMONFUNCTION.removeStorageByKey('selectCustInfo');
		})
	},
	methods:{
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: [ 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					this.custInfo.photo=res.tempFilePaths[0];
					this.changedImage=true;
				}
			});
		},
		save(){
			if(!this.custInfo.photo){
				this.$refs.notify.show('请设置头像', this.COLORCONFIG.danger);
				return;
			}
			if(!this.custInfo.custName){
				this.$refs.notify.show('请输入会员名称', this.COLORCONFIG.danger);
				return;
			}
			
			if(this.changedImage){
				uploadFileToOss(this.custInfo.photo, 'testImg', this.HTTPCONFIG.ossAC_brandLogo, this.HTTPCONFIG.ossPW).then(() => {
					this.custInfo.photo=this.HTTPCONFIG.ossDownloadUrl+this.getImageName(this.custInfo.photo);
					this.updateCustInfo();
				}).catch((reason) => {
				    this.$refs.notify.show(reason, this.COLORCONFIG.danger);
				})
				return;
			}
			this.updateCustInfo();
		},
		/**
		 * 获取选中图片的名称
		 */
		getImageName(name) {
		    let array = name.split("/");
		    return array[array.length - 1];
		},
		updateCustInfo(){
			updateCustInfo(this.custInfo.custCode,this.custInfo.custName,this.custInfo.photo).then(res=>{
				uni.navigateBack();
			}).catch(reason=>{
				this.$refs.notify.show(reason, this.COLORCONFIG.danger);		
			})
		}

	}
}
